.app2 ul {
  display: flex;
  flex-wrap: nowrap;
}

.app2 .tab-bar li {
  border: 1px solid gray;
  width: 50%;
  text-align: center;
  user-select: none;
}

.app2 .tab-content li {
  display: none;
}
.checked {
  background-color: burlywood;
}

.app2 .tab-content li.active {
  display: block;
}
