.app4 .circular {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  background-color: #0d85ee;
  margin-top: 8%;
  margin-left: 8%;
}
@keyframes change {
  0% {
  }
  50% {
    background-color: deeppink;
  }
  100% {
    background-color: blueviolet;
  }
}

.app4 .circular.active {
  animation: change 1s linear alternate infinite;
}
